
.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2rem;

}
ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style: none;
}
li {
    padding-right: 1.2rem;
    font-family: monospace;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
}
li:hover{
    transform: scale(1.1);
}
.login {    
    background-color: #D01C28;
    color: white;
    border: 2px solid white;
    border-radius: 0.5rem;
    height : 3rem;
    width : 6rem ; 
    opacity: 1;
    cursor: pointer;
}
.login:hover {
    
    background-color: red;
}
button:hover{
    transform: scale(1.1);
}
