.hero {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0 2rem;
}
.left {
    display: flex;
    flex-direction: column;
    font-family: monospace;
    margin-left: 3rem;
}
.left h1 {
    font-size: 6rem;
    margin-bottom: 0;
}
.left p {
    font-size: 1.6rem;
    font-weight: bolder;
    width: 68%;
}
.btn1{
    background-color: #D01C28;
    color: white;
    border: 2px solid white;
    border-radius: 0.5rem;
    height : 3rem;
    width : 6rem ; 
    opacity: 1;
    cursor: pointer;
    font-weight: 700;
    font-size: 0.9rem;
    
}
.btn2 {
    border: 2px solid white;
    border-radius: 0.5rem;
    height : 3rem;
    width : 6rem ; 
    opacity: 1;
    cursor: pointer;
    background-color: rgb(189, 186, 186);
    color:rgb(84, 80, 80);
    font-weight: 700;
    font-size: 0.9rem;
    margin-left: 2rem;
}
.right {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 4rem;
}